import React from 'react'
import './a-card.css'
import Button from '@material-ui/core/Button'
import DeleteIcon from '@material-ui/icons/Delete'
import IconButton from '@material-ui/core/IconButton'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'

const card = ({ props, data, cardinfo, edit, block, reset, remove }) => {
    console.log(cardinfo)
    

    var getInitials = function (string) {
        var names = string.split(' '),
            initials = names[0].substring(0, 1).toUpperCase();
        
        if (names.length > 1) {
            initials += names[names.length - 1].substring(0, 1).toUpperCase();
        }
        return initials;
    };

    const [anchorEl, setAnchorEl] = React.useState(null);

    function handleClick(event) {
        setAnchorEl(event.currentTarget);
    }

    function handleClose() {
        setAnchorEl(null);
    }

    return (
        <div>
            <div className="a-cards">
                {cardinfo.map(row => (
                    <div key={row.userId} className="a-card">
                        <div>
                            <IconButton
                                aria-label="More"
                                aria-controls="long-menu"
                                aria-haspopup="true"
                                onClick={handleClick}
                                className="a-card-menu"
                            >
                                <MoreVertIcon />
                            </IconButton>
                            <Menu
                                id="simple-menu"
                                className="a-card-menu-list"
                                anchorEl={anchorEl}
                                keepMounted
                                open={Boolean(anchorEl)}
                                onClose={handleClose}
                            >
                                <MenuItem onClick={ () => edit(row.userId) }>Edit user</MenuItem>
                                <MenuItem onClick={ () => block(row.userId) }>Block user</MenuItem>
                                <MenuItem onClick={ () => reset(row.userId)}>Reset user</MenuItem>
                                <MenuItem onClick={ () => remove(row.userId) }>Delete user</MenuItem>
                            </Menu>
                        </div>
                        <div className="a-initials">{(getInitials(row.firstName))}{(getInitials(row.lastName))}</div>
                        <p className="a-card-name">{row.firstName} {row.lastName}</p>
                        <p className="a-card-email">{row.email}</p>

                        <div className="a-card-foot">
                            {row.roleList.map(role=> <p className="roles">{role.name}</p> )}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default card